import { RouterModule, Routes } from '@angular/router';
import { HrComponent } from "./hr/hr.component";
import { HomeComponent } from "./home/home.component";
import { HrTest1Component } from "./hr/content/test1/test1.component";
import { HrTest2Component } from "./hr/content/test2/test2.component";

const routes: Routes = [

  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent, pathMatch: 'full' },
  { path: 'hr', component: HrComponent, children: [
    { path: '', redirectTo: 'test1', pathMatch: 'full' },
    { path: 'test1', component: HrTest1Component, pathMatch: 'full' },
    { path: 'test2', component: HrTest2Component, pathMatch: 'full' }
  ]}

];

export const routing = RouterModule.forRoot(routes);



