import { NgModule, ApplicationRef } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { FormsModule } from '@angular/forms';
import { NgSemanticModule } from "ng-semantic";

import { AppComponent } from './app.component';

import { removeNgStyles, createNewHosts } from '@angularclass/hmr';
import { MenuNavigationComponent } from "./navigation/menu/navigation.component";
import { SidebarNavigationComponent } from "./navigation/sidebar/navigation.component";
import { routing } from "./app.routing";
import { HomeComponent } from "./home/home.component";
import { HrComponent } from "./hr/hr.component";
import { HrTest1Component } from "./hr/content/test1/test1.component";
import { HrTest2Component } from "./hr/content/test2/test2.component";

@NgModule({
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    NgSemanticModule,
    routing
  ],
  declarations: [
    AppComponent,
    HomeComponent,
    HrComponent,
    HrTest1Component,
    HrTest2Component,
    MenuNavigationComponent,
    SidebarNavigationComponent
  ],
  bootstrap: [AppComponent]
})
export class AppModule {

  constructor(public appRef: ApplicationRef) {}

  /**
   * Hot module replacement (HMR) integration
   */

  hmrOnInit(store) {
    console.log('HMR store', store);
  }

  hmrOnDestroy(store) {

    let cmpLocation = this.appRef.components.map(cmp => cmp.location.nativeElement);

    // recreate elements
    store.disposeOldHosts = createNewHosts(cmpLocation);

    // remove styles
    removeNgStyles();
  }

  hmrAfterDestroy(store) {

    // display new elements
    store.disposeOldHosts();
    delete store.disposeOldHosts;
  }
}



