import {Component, AfterViewInit, ViewEncapsulation} from '@angular/core';
import { Router, Event, NavigationStart } from "@angular/router";


@Component({
    selector: 'app-menu-navigation',
    template: require('./navigation.html'),
    styles: [require('./navigation.component.css')],
    encapsulation: ViewEncapsulation.None
})
export class MenuNavigationComponent implements AfterViewInit {

    hrRouteActive: boolean = true;

    readonly greysonButtonTitle = "Greyson";

    constructor( private router: Router) {
        // ...
    }

    ngAfterViewInit(): void {


        this.router.events.subscribe( (event: Event) => {

            if (event instanceof NavigationStart) {

                // console.log('started navigation: ' + event.url);
                this.hrRouteActive = /(.*)\/hr(.*)/g.test(event.url);
                // console.log('hrRouteActive: ' + this.hrRouteActive);
            }
        });

        // console.log('navigation component initialized!')
    }
}



